import React from "react";

const Saver = (props) => {
    const saveFile = () => {
        props.onSave();
    };

    return (
        <a
            className="button is-success"
            onClick={saveFile}
            download={props.filename.replace(/\.[^/.]+$/, "") + ".json"}
            href={props.dllink}>
            
        </a>
    )
};

export default Saver;


