import React from "react";
import "bulma/css/bulma.css";
import { useToasts } from "react-toast-notifications";

import { Schema, KEYCODES_SHORTCUT } from "../helpers/consts";

const ID_CATEGORY_SEL = "category-selector";

const SelectedText = (props) => {
    const { addToast } = useToasts();

    const handleDelete = () => {
        props.onDelete();
    };
    const handleAdd = () => {
        try {
            const annovalue = document.getElementById(ID_CATEGORY_SEL).value;
            props.onAdd(annovalue);
        } catch (e) {
            console.log(e);
            if ( e.message !== "RangeError") {
                addToast("", {appearance: "error"});
            } else {
                throw e;
            }
        }
    }

    let options = [];
    for (const idx in Schema.IndexedAnnotag) {
        const key = Schema.IndexedAnnotag[idx];
        options.push(
            <option key={key} value={key}>
                {Schema.Annotag[key] + (
                    idx < KEYCODES_SHORTCUT.length ? " (" + String.fromCharCode(KEYCODES_SHORTCUT[idx]) + ")": "")}
            </option>
        )
    }

    return (
        <div className="columns">
            {/* <div className="column" align="left">
                &emsp;&emsp;&emsp;{1+textinfo.lid} <br />
                &emsp;&emsp;{textinfo.from}{textinfo.to} <br />
                {textinfo.text}
            </div> */}
            <div className="column">
                <div className="columns is-gapless">
                    <div className="column">
                        <div className="select">
                            <select id={ID_CATEGORY_SEL}>
                                {options}
                            </select>
                        </div>
                        <button
                            className="button"
                            onClick={handleAdd}>
                            
                        </button>
                        <button
                            className="button is-danger"
                            onClick={handleDelete}>
                             (BS)
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default SelectedText;


